import { Box, Button, TextField, Typography } from "@mui/material";
import { experimentalStyled as styled } from "@mui/material/styles";
import Paper from "@mui/material/Paper";
import Grid from "@mui/material/Grid";
import { minHeight } from "@mui/system";
import { useNavigate } from "react-router-dom";

function MainBodyBox() {
  const Item = styled(Paper)(({ theme }) => ({
    backgroundColor: theme.palette.mode === "dark" ? "#1A2027" : "#fff",
    ...theme.typography.body2,
    padding: theme.spacing(2),
    textAlign: "center",
    color: theme.palette.text.secondary,
    height: "50vh",
    width: "20vw",
  }));
  const navi = useNavigate();

  const goToInventory = () => {
    navi("/inventory");
  };

  return (
    <Box sx={{ display: "flex", flexDirection: "column", m: 3 }}>
      <Box sx={{ display: "flex", flexDirection: "row" }}>
        <Box sx={{ border: "1px solid #999999", width: "100%", p: 2 }}>
          <Typography variant="h4" sx={{ fontWeight: "bold" }}>
                
          </Typography>
          <Typography sx={{ pt: 3 }}>
                    .
          </Typography>
          <Button variant="contained" sx={{ mt: 5 }} onClick={goToInventory}>
            
          </Button>
        </Box>
        <Box
          sx={{ width: "400px", height: "250px", border: "1px solid #999999" }}
        >
          <img
            src={"./images/mainbodypic.jpg"}
            width="100%"
            height="100%"
            alt="none"
            style={{ objectFit: "cover" }}
          />
        </Box>
      </Box>
      <Box sx={{ flexGrow: 1, width: "100%", pt: 2 }}>
        <Grid
          container
          spacing={{ xs: 2, md: 3 }}
          columns={{ xs: 6, sm: 6, md: 12 }}
        >
          {["page1", "page2", "page3", "page4"].map((one, index) => (
            <Grid item xs={6} sm={4} md={3} key={index}>
              <Item elevation={3}>{one}</Item>
            </Grid>
          ))}
        </Grid>
      </Box>
    </Box>
  );
}

export default MainBodyBox;
